import React, { useState } from 'react';
import axios from 'axios';  // axios import 
import styles from './Transform.module.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCopy, faVolumeHigh, faXmark } from '@fortawesome/free-solid-svg-icons';

function Transform() {
  const [inputText, setInputText] = useState('');
  const [outputText, setOutputText] = useState('');  //    
  const [showCopyMessage, setShowCopyMessage] = useState(false);
  const [isLoading, setIsLoading] = useState(false);  //   
  const [isPlaying, setIsPlaying] = useState(false);

  //   
  const handleTransform = async () => {
    if (!inputText.trim()) return;
    
    setIsLoading(true);
    try {
      const response = await axios.post('http://localhost:8000/api/chat', {
        message: inputText,
        style: document.querySelector(`.${styles.styleSelect}`).value
      });
      
      setOutputText(response.data.response);
    } catch (error) {
      console.error('Error:', error);
      setOutputText(' .  .');
    }
    setIsLoading(false);
  };

  const handleReset = () => {
    setInputText('');
    setOutputText('');  //  
  };

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(outputText);
      setShowCopyMessage(true);
      setTimeout(() => {
        setShowCopyMessage(false);
      }, 2000);
    } catch (err) {
      console.error(' :', err);
    }
  };

  // handlePlaySound  
  const handlePlaySound = async () => {
    if (!outputText || isPlaying) return;
    
    setIsPlaying(true);
    try {
      const selectedStyle = document.querySelector(`.${styles.styleSelect}`).value;
      const response = await axios.post('http://localhost:8000/api/tts', {
        text: outputText,
        voice: {
          style: selectedStyle  //    
        }
      }, {
        responseType: 'arraybuffer'
      });

      const audioContext = new (window.AudioContext || window.webkitAudioContext)();
      const audioBuffer = await audioContext.decodeAudioData(response.data);
      const source = audioContext.createBufferSource();
      source.buffer = audioBuffer;
      source.connect(audioContext.destination);
      
      source.onended = () => {
        setIsPlaying(false);
      };
      
      source.start(0);
    } catch (error) {
      console.error('TTS Error:', error);
      setIsPlaying(false);
    }
  };

  return (
    <div className={styles.container}>
      <div className={styles.transformBox}>
        <div className={styles.leftSection}>
          <h3></h3>
          <div className={styles.inputWrapper}>
            <textarea 
              className={styles.inputArea} 
              placeholder="  "
              value={inputText}
              onChange={(e) => setInputText(e.target.value)}
            />
            {inputText && (
              <button className={styles.resetButton} onClick={handleReset}>
                <FontAwesomeIcon icon={faXmark} />
              </button>
            )}
          </div>
          <button 
            className={styles.transformButton} 
            onClick={handleTransform}
            disabled={isLoading || !inputText.trim()}
          >
            {isLoading ? ' ...' : ''}
          </button>
        </div>
        
        <div className={styles.rightSection}>
          <div className={styles.selectWrapper}>
            <select className={styles.styleSelect}>
              <option value="formal"></option>
              <option value="casual"></option>
              <option value="polite"></option>
              <option value="cute"></option>
            </select>
          </div>
          <textarea 
            className={styles.outputArea} 
            value={outputText}
            readOnly={true}
          ></textarea>
          <div className={styles.buttonGroup}>
            <button 
              className={styles.soundButton}
              onClick={handlePlaySound}
              disabled={!outputText || isPlaying}
            >
              <FontAwesomeIcon icon={faVolumeHigh} />
              <span className={styles.srOnly}>
                {isPlaying ? ' ...' : ' '}
              </span>
            </button>
            <div className={styles.copyButtonWrapper}>
              <button 
                className={styles.copyButton} 
                onClick={handleCopy}
                disabled={!outputText}
              >
                <FontAwesomeIcon icon={faCopy} />
                <span className={styles.srOnly}></span>
              </button>
              {showCopyMessage && (
                <div className={styles.copyMessage}></div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Transform;